import { Component, Inject, OnInit, ViewEncapsulation, ElementRef, Input, ViewChild, ChangeDetectorRef } from '@angular/core';
import {
    Space,
    RoomResultsViewModel,
    BookingStoreState,
    User,
    Booking,
    BookingFormData,
    BOOKING_STATUS,
    checkCollisions,
    SpacesService,
    BookingsService,
    BookingStateService,
    clearBookingFormData,
    ApplicationService,
    selectFilteredByIdResults,
    FilteredSpaces,
    selectActiveForm,
    roomFormDataToQuery,
    storeBookingFormData,
} from '@mckinsey-converge/data-common';
import { MAP_OVERLAY_DATA } from '@acaprojects/ngx-interactive-map';
import { DateTime } from 'luxon';
import { Point, BaseDirective, closestToTimeSlot, HashMap, closestToTimeSlotTz } from '@mckinsey-converge/base';
import { BehaviorSubject, Observable, interval, timer, of } from 'rxjs';
import { CENTER_POINT, MIN_ZOOM_LEVEL } from 'src/app/map/map.utils';
import { Store, select } from '@ngrx/store';
import { RoomStoreState, loadByIdResults, loadRoomsForResults } from '@mckinsey-converge/rooms';
import { BuildingStoreState } from '@mckinsey-converge/buildings';
import { UserStoreState } from '@mckinsey-converge/user';
export type RoomResultsState = BookingStoreState &
    RoomStoreState &
    BuildingStoreState &
    UserStoreState;
import { selectCurrentUser } from '@mckinsey-converge/user';
import { delay, map, take } from 'rxjs/operators';
import { DateTZ } from '@mckinsey-converge/date-tz';
import { WAITING_TIME } from '../floor-map.utils';

interface SpaceWithReturnData extends Space {
    // callback?: Function,
    room?: RoomResultsViewModel;
    parentComponent?: any;
    roomObs?: Observable<RoomResultsViewModel>
}
/**
 * Replaces the built-in {@link MapPinComponent} based on design.
 */
@Component({
    selector: 'foor-map-overlay',
    template: `
        <div class="mck-column mck-center map-overlay-container">
          <div  class="overlay-modal flex-coloum">

            <div class="room-availability-header flex-row flex-center">
							<img *ngIf="isAvailable" src="assets/icon/book-available-room.svg" />
							<img  *ngIf="!isAvailable"  src="assets/img/room-is-booked-kiosk.png"  />
             	<button class="close">
                <img src="assets/img/close_outline.svg" (click)="closeModal()" (touchend)="closeModal()" />
              </button>
            </div>

						<div *ngIf="isAvailable" class="flex-coloum container" >
							<div class="availability-text-head flex-row flex-center">
								<span>This room is available</span>
							</div>
							<div class="availability-text flex-row flex-center">
								<span>Room {{ data.simple_name || data.settings.room_name || data.name || '' }} is available for booking</span>
							</div>
							<div class="availability-text flex-row flex-center">
								<span>Book now for:</span>
							</div>
							<div class="flex-row flex-center">
								<button mat-button color="primary" class="mck-button mck-short mck-column mck-center mck-primary" *ngIf="diff - 15 >= 15" (click)="onBook(15)" (touchend)="onBook(15)" >
										{{ this.start_epoch }}-{{ this.end_epoch_15 }} (15 minutes)
								</button>
							</div>
							<div class="flex-row flex-center">
									<button mat-button color="primary" class="mck-button mck-short mck-column mck-center mck-primary" *ngIf="diff >= 30" (click)="onBook(30)" (touchend)="onBook(30)">
									{{ this.start_epoch }}-{{ this.end_epoch_30 }} (30 minutes)
								</button>
							</div>
							<div class="availability-text flex-row flex-center" *ngIf="!this.untilNextBookingFlag"> 
								<!--  -->
								<span >Until next booking</span>
							</div>
							<div class="flex-row flex-center"  *ngIf="!this.untilNextBookingFlag">
								<!--  -->
								<button mat-button color="primary" class="mck-button mck-short mck-column mck-center mck-primary" 
									(click)="onBook(diffInMs / (60 * 1000) - 6)" (touchend)="onBook(diffInMs / (60 * 1000) - 6)" >
										{{ diffInHours }} hours {{ diffInMinutes }} minutes
								</button>
							</div>

							<div class="until-next flex-row flex-center"  *ngIf="isTimerActive">
									<span> Available if you book in the next {{minutesRemaining}}:{{secondsRemaining}} minutes</span> 
							</div>
							<div class="until-next flex-row flex-center" *ngIf="!isTimerActive">
									Refresing availability ...
							</div>
          	</div>
						<div *ngIf="!isAvailable" class=" flex-column container">
								
							<div class="availability-text-head flex-row flex-center">
									<span>This room is booked</span>
							</div>
							<div class="availability-text">
									<span> Apologies, this room has been booked recently by another user, please try another one.   </span>
							</div>
							<div class="flex-row flex-center footer">
								<button mat-button color="primary" class="mck-button mck-short mck-column mck-center mck-primary" (click)="closeModal()" (touchend)="closeModal()" >
										Check availability
								</button>
							</div>
						</div>
          </div>
          <div class="map-icon"></div>
        </div>
    `,
    styleUrls: ['./floor-map-overlay.component.scss'],
    encapsulation: ViewEncapsulation.Emulated,
})
export class FloorMapOverlayComponent extends BaseDirective implements OnInit {
    public selectedRoomId = this.data.id;

    public zoom = new BehaviorSubject(MIN_ZOOM_LEVEL);
    public center = new BehaviorSubject<Point>(CENTER_POINT);

    public date = DateTime.now().valueOf();
    /** Current User */
    public currentUser: User;

    public availableUntil = parseInt(this.data.settings.available_until.toString()) * 1000;
    public diff: any;

    public latestBooking: any[];
    public latestBookingStartTime: string;
    public diffInHours: number;
    public diffInMinutes: number;
    public booking: Booking = new Booking({});
    public loading: boolean;
    public bookingData = new BehaviorSubject<BookingFormData | undefined>(undefined);
    public confirmed: boolean = false;
    /** Whether booking has succeeded */
    public success: boolean;
    public diffInMs: number;
    public allDayRoomBookings: FilteredSpaces;
    public futureBookingsForTheDay: HashMap<any>[];
    public untilNextBookingFlag: boolean = true;
    public start_epoch: any;
    public end_epoch_15: any;
    public end_epoch_30: any;

    public userSelectedRoom: RoomResultsViewModel; 

    // @ViewChild('time', { static: false }) public time: ElementRef;
    public isTimerActive: boolean = true;
    minutesRemaining: number | string;
    secondsRemaining: number | string;
    isAvailable: boolean = true;
    intervalCall_15: any;
    intervalCall_1: any ;
		timerIndex = 0;
		public currentMin;

    constructor(
        @Inject(MAP_OVERLAY_DATA) public data: SpaceWithReturnData,
        private elementRef: ElementRef,
        public store: Store<RoomResultsState>,
        public _spaces: SpacesService,
        public _bookings_service: BookingsService,
        public _booking_state_service: BookingStateService,
        public _service: ApplicationService,
        // public cdRef: ChangeDetectorRef,
    ) {
        super();
    }

    public updateRoomData(space) {
        this.userSelectedRoom = space;
   
            this.isTimerActive = true;
            // this.cdRef.detectChanges();
            this.availableUntil = parseInt(this.userSelectedRoom.space.settings.available_until.toString()) * 1000;
            const currentTime = new Date(DateTime.now().valueOf());
            const endTime = new Date(this.availableUntil);
            this.diff = Math.abs(Math.round((endTime.getTime() - currentTime.getTime()) / 60000));

            // inform user if space has been booked by other
            if(this.data.parentComponent.roomStatus.statusMap[this.selectedRoomId] !== 'available') {
                this.isAvailable = false;
                this.intervalCall_1?.unsubscribe();
                // clearInterval(this.intervalCall_1);
                // this.intervalCall_15?.unsubscribe();
                clearInterval(this.intervalCall_15);
            }

    }

   
    public setup15MinTimer() {
        //interval for 15 min
        !this.timerIndex && setTimeout(()=>this.setUpTime(), 1100 ) ;
        this.timerIndex++
        this.intervalCall_15 = window.setInterval(
            () => { this.intervalCall_1?.unsubscribe(); setTimeout(()=>this.setUpTime(), 1100 ) }
            , ( 1000 * 60 * WAITING_TIME ) );
       

    }

    public setUpTime() {
        // console.log(DateTime.now().setZone(this.userSelectedRoom?.building?.timezone).get('minute'));
        // console.log(DateTime.now().setZone(this.userSelectedRoom?.building?.timezone).get('minute') % WAITING_TIME)
        // console.log(DateTime.now().setZone(this.userSelectedRoom?.building?.timezone).get('second'))
        const addMin = DateTime.now().get('minute') % WAITING_TIME === 0 ? 1 : 0 ;
        const addSeconds = 60 - DateTime.now().get('second');
        let start:any = closestToTimeSlot(DateTime.now().plus({ minutes: addMin, seconds:0 }).setZone(this.userSelectedRoom?.building?.timezone), WAITING_TIME);
            start = start.toLocaleString(DateTime.TIME_SIMPLE);
            if(start !== this.start_epoch ) {
                
                this.start_epoch = start;
                
                this.availableUntil = parseInt(this.userSelectedRoom.space.settings.available_until.toString()) * 1000;
                const currentTime = new Date(DateTime.now().valueOf());
                const endTime = new Date(this.availableUntil);
                this.diff = Math.abs(Math.round((endTime.getTime() - currentTime.getTime()) / 60000));
    
                const addMin = DateTime.now().get('minute') % WAITING_TIME === 0 ? 1 : 0 ;
                const addSeconds = 60 - DateTime.now().get('second');
                this.startTimer(addMin);
    
            }
            
            let end_15: any = closestToTimeSlot(DateTime.now().plus({ minutes: addMin, seconds:0 }).setZone(this.userSelectedRoom?.building?.timezone), WAITING_TIME).plus({ minutes: 15 });
                end_15 = end_15.toLocaleString(DateTime.TIME_SIMPLE);
            if(end_15 !== this.end_epoch_15) {
                this.end_epoch_15 = end_15
            }
    
            let end_30: any = closestToTimeSlot(DateTime.now().plus({ minutes: addMin, seconds:0 }).setZone(this.userSelectedRoom?.building?.timezone), WAITING_TIME).plus({ minutes: 30 });
                end_30 = end_30.toLocaleString(DateTime.TIME_SIMPLE);
            if(end_30 !== this.end_epoch_30) {
                this.end_epoch_30 = end_30;
            }
    }

    public ngOnInit(): void {
        this.fetchRooms(false);

        this.data.parentComponent.childCallback = this.updateRoomData.bind(this);
        this.userSelectedRoom = this.data?.room;

        this.subscription(
            'currentUser',
            this.store
                .select(selectCurrentUser)
                .pipe(take(1))
                .subscribe((user: User | undefined) => {
                    // Once we know we have the user loaded.
                    this.currentUser = new User(user);
                })
        );
        const currentTime = new Date(this.date);
        const endTime = new Date(this.availableUntil);
        this.diff = Math.abs(Math.round((endTime.getTime() - currentTime.getTime()) / 60000));

        const addMin = DateTime.now().get('minute') % WAITING_TIME === 0 ? 1 : 0 ;
        const addSeconds = 60 - DateTime.now().get('second');

        this.start_epoch = closestToTimeSlot(DateTime.now().plus({ minutes: addMin, seconds:0 }).setZone(this.userSelectedRoom?.building?.timezone), WAITING_TIME);
        this.start_epoch = this.start_epoch.toLocaleString(DateTime.TIME_SIMPLE);
        this.end_epoch_15 = closestToTimeSlot(DateTime.now().plus({ minutes: addMin, seconds:0 }).setZone(this.userSelectedRoom?.building?.timezone), WAITING_TIME).plus({ minutes: 15 });
        this.end_epoch_15 = this.end_epoch_15.toLocaleString(DateTime.TIME_SIMPLE);
        this.end_epoch_30 = closestToTimeSlot(DateTime.now().plus({ minutes: addMin, seconds:0 }).setZone(this.userSelectedRoom?.building?.timezone), WAITING_TIME).plus({ minutes: 30 });
        this.end_epoch_30 = this.end_epoch_30.toLocaleString(DateTime.TIME_SIMPLE);
        // this.cdRef.detectChanges();
        //get all the bookings for selected room for the day from the store
        this.subscription(
            'bookingSpaceResults',
            this.store.pipe(select(selectFilteredByIdResults), delay(10)).subscribe((next) => {
                if (next) {
                    this.allDayRoomBookings = next.success;
                    this.updateLatestBooking();
                }
            })
        );
        // console.log(this.diff);
        this.data.parentComponent.isOverlayOpened = true;
    }

    ngAfterViewInit() {
        // const callDuration = this.time.nativeElement;
        console.log(DateTime.now().get('second'))
        const addMin = DateTime.now().setZone(this.userSelectedRoom?.building?.timezone).get('minute') % WAITING_TIME === 0 ? 1 : 0 ;
        const addSeconds = 60 - DateTime.now().get('second');
        this.startTimer(addMin);
    }

    
    // countdown timer for coachmark
    public startTimer(display = 0, addSeconds = 0) {
       
        // const currentTime = new Date(DateTime.now().valueOf());
        // const endTime = new Date(closestToTimeSlot(DateTime.now().plus({minutes: display, seconds: addSeconds}).setZone(this.userSelectedRoom?.building?.timezone), WAITING_TIME).valueOf());
        // const availableTimeToBook = Math.abs((endTime.getTime() - currentTime.getTime()) / 60000)* 60;
        // let timer = availableTimeToBook; //(this.diff -35) * 60;
        // let minutes;
        // let seconds;
        let timer = 0 ;
        if(timer >= 0){
            this.intervalCall_1 =  interval(1000).subscribe(x => {

                const currentTime = new Date(DateTime.now().valueOf());
                const endTime = new Date(closestToTimeSlot(DateTime.now().plus({minutes: display, seconds: addSeconds}).setZone(this.userSelectedRoom?.building?.timezone), WAITING_TIME).valueOf());
                const availableTimeToBook = Math.abs((endTime.getTime() - currentTime.getTime()) / 60000)* 60;
                let timer = availableTimeToBook; //(this.diff -35) * 60;

                this.isTimerActive = true;
                this.minutesRemaining = Math.floor(timer / 60);
                this.secondsRemaining = Math.floor(timer % 60);

                this.minutesRemaining = this.minutesRemaining < 10 ? "0" + this.minutesRemaining : this.minutesRemaining;
                this.secondsRemaining = this.secondsRemaining < 10 ? "0" + this.secondsRemaining : this.secondsRemaining;
                
                --timer;
                
                if (timer <= 0) {
                    timer = 0
                    this.intervalCall_1?.unsubscribe();
                    // clearInterval(this.intervalCall_1);
                    this.isTimerActive = false;
                    !this.timerIndex && this.setup15MinTimer();
                   
										this.store.select(selectActiveForm).pipe(take(1))
											.subscribe((form) => {
													const addMin = DateTime.now().setZone(this.userSelectedRoom?.building?.timezone).get('minute') % WAITING_TIME === 0 ? 1 : 0 ;
													const addSeconds = 60 - DateTime.now().setZone(this.userSelectedRoom?.building?.timezone).get('second');
													form['date'] =  closestToTimeSlot(DateTime.now().setZone(this.userSelectedRoom?.building?.timezone).plus({ minutes: addMin, seconds:0 }), WAITING_TIME).valueOf(),
													form.dateTz = closestToTimeSlotTz(
															new DateTZ({ date: DateTime.now().plus({ minutes: addMin, seconds:0 }), is_local_tz: false, building_tz: form.timezone }),
															WAITING_TIME
													)
													this.store.dispatch(storeBookingFormData({ payload: form }));
													
													this.store.dispatch(loadRoomsForResults.request(roomFormDataToQuery(form)));
											
											});

                }
            }) //, 1000, endTime)
        }
    }

    public updateLatestBooking() {
        if (this.allDayRoomBookings?.spaces) {
            this.futureBookingsForTheDay =
                this.allDayRoomBookings?.spaces[0]?.settings?.bookings.filter((item) => {
                    if (item.start_epoch * 1000 > new Date(this.date).getTime()) {
                        return item;
                    }
                });
            const latestBookingStartTime = new Date(
                this.futureBookingsForTheDay[0]?.start_epoch * 1000
            );

            this.diffInMs = latestBookingStartTime?.getTime() - new Date(this.date).getTime();

            const diffInHours = Math.floor(this.diffInMs / (1000 * 60 * 60));
            const diffInMinutes = Math.floor((this.diffInMs % (1000 * 60 * 60)) / (1000 * 60));
            this.diffInHours = diffInHours;
            this.diffInMinutes = diffInMinutes;
            this.untilNextBookingFlag = Number.isNaN(this.diffInMs);
        }
    }

    //fetch all the bookings for selected room for the day to the store
    public fetchRooms(allWeek: boolean = true) {
        const day = 24 * 60;
        const week = day * 7;
        const date = (this.booking?.startDateTz).clone();
        const startOfDay = date.startOfValue('day').setValue({ second: 0, millisecond: 0 });
        const startOfWeek = date.startOfValue('week');

        // Query for the entire day for this room
        const query = {
            dateTz: allWeek ? startOfWeek : startOfDay,
            duration: allWeek ? week : day,
            locations: this.data?.id,
            capacity: this.booking?.space?.capacity,
        };
        this.store.dispatch(loadByIdResults.request(query));
    }

    public closeModal() {
        const modal = this.elementRef.nativeElement.querySelector('.map-overlay-container');
        modal.classList.add('hide');
        this.data.parentComponent.isOverlayOpened = false;
        this.timerIndex = 0;
        this.isAvailable = false;
        this.intervalCall_1?.unsubscribe();
        // clearInterval(this.intervalCall_1);
        // this.intervalCall_15?.unsubscribe();
        clearInterval(this.intervalCall_15);
    }

    public roomDataCallback() {
        if (this.data.parentComponent.roomSelected && this.userSelectedRoom) {
            this.data.parentComponent.roomSelected(this.userSelectedRoom);
        }
    }

    public onBook(minutes: number) {
        minutes = Math.round(minutes);
        const {filters, settings} = this.userSelectedRoom.space;
        if ( (filters && filters?.equipment_code ) || (settings && settings?.equipment_code) ) {
            this.roomDataCallback();
        } else {
            this.saveChanges(minutes);
        }
    }

    public bookingFormToBooking(minutes): Booking {
        const booking_form_data = this.bookingData.value;
        const has_note_buffer =
            booking_form_data?.notes && booking_form_data.notes?.length
                ? { [booking_form_data.selectedRoom.space.email]: 900 }
                : {}; // 900 seconds = 15 min
        const attendees = this.booking.attendees;
        const company = this.booking.company;
        const start_epoch = closestToTimeSlot(DateTime.now(), 15).valueOf() / 1000;
        const end_epoch =
            closestToTimeSlot(DateTime.now(), 15).plus({ minutes: minutes }).valueOf() / 1000;

        const raw_data = {
            space: { id: this.selectedRoomId },
            all_day: false,
            attendees,
            booking_type: booking_form_data?.bookingType.value,
            catering: [],
            start_epoch,
            end_epoch,
            timezone: booking_form_data?.timezone,
            equipment_codes: {
                [booking_form_data?.selectedRoom.space.email]: booking_form_data?.code,
            },
            expected_attendees: {
                [this.data.email]: 1,
            },
            has_catering: false,
            needs_space: false,
            notes: this.booking.notes,
            organiser: this.currentUser,
            creator: this.currentUser,
            room: this.userSelectedRoom.space,
            title: 'Ad hoc meeting',
            setup: has_note_buffer,
            breakdown: has_note_buffer,
            company,
        };
        console.log(new Booking(raw_data));
        return new Booking(raw_data);
    }

    //function for sending final booking request
    public saveChanges(minutes) {
        this.loading = true;
        this.booking = this.bookingFormToBooking(minutes);
        // const booking_form_data = this.bookingData.value;
        this.booking.status = BOOKING_STATUS.approved;
        checkCollisions(this._spaces, this.booking).then(
            (bookable: boolean) => {
                if (!bookable) {
                    // TODO - Refactor for https://acaprojects.atlassian.net/browse/MCK-1498
                    // API is retuning bookings ouside of requested time range
                    // TODO check booking date and times for conflict manually
                    this.booking.status = BOOKING_STATUS.unavailable;
                    return;
                }

                this._bookings_service.save(this.booking).then(
                    (bkn) => {
                        this.loading = false;
                        this.success = true;
                        this.confirmed = true;
                        this._booking_state_service.replace(bkn);

                        // When saved, the returned booking has a null room.
                        // Correct this so the rest of the code doesn't flake out.
                        this.booking = new Booking({ ...bkn.toJSON(), room: this.booking.room });
                        // Clear the current booking form
                        this.store.dispatch(clearBookingFormData());

                        // Heap track event
                        this._service.analytics.track('create', {
                            booking_id: bkn.id,
                            office: this.booking.room.building.name,
                            room: this.booking.room.local_name,
                            start_time: bkn.startDateTz.ms,
                            end_time: bkn.endDateTz.ms,
                            catering: !!bkn.catering.length,
                            headcount: 1,
                        });
                        this.data.parentComponent.confirmBooking(this.booking);
                    },
                    (err) => {
                        this.loading = false;
                        this.confirmed = false;
                        if (err.status === 409) {
                            // Unavailable Confirmation
                            this.booking.status = BOOKING_STATUS.unavailable;
                            this._booking_state_service.replace(this.booking);
                            this.confirmed = true;
                            this.success = false;
                        } else {
                            console.log(err);
                        }
                    }
                );
            },
            (err) => {
                // Unavailable Confirmation
                this.loading = false;
                this.timeout(
                    'error',
                    () => {
                        this.booking.status = BOOKING_STATUS.unavailable;
                        this._booking_state_service.replace(this.booking);
                        this.confirmed = true;
                        this.success = false;
                    },
                    500
                );
            }
        );
    }
}
